/**
 * EnhancedCrypto v2.1 - Systme cryptographique multi-utilisateurs complet
 *  FLUX : RSA pour cl AES  AES-GCM pour messages  IndexedDB persistance
 *  FIX : Pas de dpendance timing, sessions par userId, retry automatique
 */

export class EnhancedCrypto {
  constructor(userId) {
    this.userId = userId;
    this.sessions = new Map(); // userId -> {sessionKey, encryptedSessionKey, counter, createdAt}
    this.db = null;
    this.initDB();
  }

  /**
   * Gnre une paire de cls d'identit RSA-OAEP (pour chiffrer la cl AES de session)
   * Retourne aussi la cl publique au format JWK (facile  transmettre).
   */
  async generateIdentityKeys() {
    const { publicKey, privateKey } = await window.crypto.subtle.generateKey(
      {
        name: 'RSA-OAEP',
        modulusLength: 2048,
        publicExponent: new Uint8Array([1, 0, 1]),
        hash: 'SHA-256'
      },
      true,
      ['encrypt', 'decrypt']
    );

    const publicKeyJWK = await window.crypto.subtle.exportKey('jwk', publicKey);
    return { publicKey, privateKey, publicKeyJWK };
  }

  /**
   * Gnre une cl AES-256-GCM pour la session.
   */
  async generateSessionKey() {
    return window.crypto.subtle.generateKey(
      { name: 'AES-GCM', length: 256 },
      true,
      ['encrypt', 'decrypt']
    );
  }

  //  IndexedDB persistance des sessions
  async initDB() {
    return new Promise((resolve, reject) => {
      //  Nouveau nom de DB pour viter les anciennes sessions incompatibles
      const request = indexedDB.open('CryptoSessionsDB_v3', 1);
      
      request.onerror = () => reject(request.error);
      request.onsuccess = () => {
        this.db = request.result;
        this.loadSessions().then(resolve).catch(reject);
      };

      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        if (!db.objectStoreNames.contains('sessions')) {
          db.createObjectStore('sessions', { keyPath: 'userId' });
        }
      };
    });
  }

  async loadSessions() {
    if (!this.db) return;
    try {
      const tx = this.db.transaction(['sessions'], 'readonly');
      const store = tx.objectStore('sessions');
      const allSessions = await new Promise(resolve => {
        const request = store.getAll();
        request.onsuccess = () => resolve(request.result);
      });

      // Rimporter les cls AES
      for (const sessionData of allSessions) {
        const sessionKey = await window.crypto.subtle.importKey(
          'raw',
          this.base64ToArrayBuffer(sessionData.sessionKeyRaw),
          { name: 'AES-GCM', length: 256 },
          true,
          ['encrypt', 'decrypt']
        );
        
        this.sessions.set(sessionData.userId, {
          sessionKey,
          encryptedSessionKey: sessionData.encryptedSessionKey,
          messageCounter: sessionData.messageCounter || 0,
          createdAt: sessionData.createdAt
        });
      }
      console.log(` ${allSessions.length} sessions charges depuis IndexedDB`);
    } catch (error) {
      console.error('Erreur load sessions:', error);
    }
  }

  async saveSession(userId, sessionData) {
    if (!this.db) return;
    try {
      const sessionKeyRaw = await window.crypto.subtle.exportKey('raw', sessionData.sessionKey);
      const tx = this.db.transaction(['sessions'], 'readwrite');
      const store = tx.objectStore('sessions');
      
      await new Promise((resolve, reject) => {
        const request = store.put({
          userId,
          sessionKeyRaw: this.arrayBufferToBase64(sessionKeyRaw),
          encryptedSessionKey: sessionData.encryptedSessionKey,
          messageCounter: sessionData.messageCounter || 0,
          createdAt: sessionData.createdAt
        });
        request.onsuccess = resolve;
        request.onerror = reject;
      });
    } catch (error) {
      console.error('Erreur save session:', error);
    }
  }

  /**
   *  initSession CORRIGE : Vrifie existence + persistance
   */
  async initSession(recipientId, recipientPublicKeyJWK) {
    //  CHECK 1 : Session dj existante ?
    if (this.sessions.has(recipientId)) {
      console.log(` Session dj active: ${recipientId}`);
      return this.sessions.get(recipientId);
    }

    try {
      //  CHECK 2 : IndexedDB
      await this.loadSessions();
      if (this.sessions.has(recipientId)) {
        console.log(` Session restaure depuis DB: ${recipientId}`);
        return this.sessions.get(recipientId);
      }

      //  NOUVELLE SESSION
      const sessionKey = await this.generateSessionKey();

      // Importer cl publique RSA destinataire
      const recipientPublicKey = await window.crypto.subtle.importKey(
        'jwk',
        recipientPublicKeyJWK,
        { name: 'RSA-OAEP', hash: 'SHA-256' },
        true,
        ['encrypt']
      );

      // Chiffrer la cl AES avec RSA du destinataire
      const sessionKeyRaw = await window.crypto.subtle.exportKey('raw', sessionKey);
      const encryptedSessionKeyBuffer = await window.crypto.subtle.encrypt(
        { name: 'RSA-OAEP' },
        recipientPublicKey,
        sessionKeyRaw
      );

      const sessionData = {
        sessionKey,
        encryptedSessionKey: this.arrayBufferToBase64(encryptedSessionKeyBuffer),
        messageCounter: 0,
        createdAt: Date.now()
      };

      //  PERSISTANCE IMMDIATE
      this.sessions.set(recipientId, sessionData);
      await this.saveSession(recipientId, sessionData);

      console.log(` Session CRE & SAUVEGARDE: ${recipientId}`);
      return sessionData;

    } catch (error) {
      console.error(` initSession ${recipientId}:`, error);
      throw error;
    }
  }

  /**
   *  acceptSession CORRIGE : Reconstruction automatique de la cl AES
   */
  async acceptSession(senderId, encryptedSessionKeyBase64, myPrivateKey) {
    // CHECK existence
    if (this.sessions.has(senderId)) {
      console.log(` Session dj active avec ${senderId}`);
      return;
    }

    try {
      const encryptedSessionKey = this.base64ToArrayBuffer(encryptedSessionKeyBase64);

      // Dchiffrer la cl AES avec notre cl prive RSA
      const sessionKeyRaw = await window.crypto.subtle.decrypt(
        { name: 'RSA-OAEP' },
        myPrivateKey,
        encryptedSessionKey
      );

      // Importer la cl AES
      const sessionKey = await window.crypto.subtle.importKey(
        'raw',
        sessionKeyRaw,
        { name: 'AES-GCM', length: 256 },
        true,
        ['encrypt', 'decrypt']
      );

      const sessionData = {
        sessionKey,
        encryptedSessionKey: encryptedSessionKeyBase64,
        messageCounter: 0,
        createdAt: Date.now()
      };

      //  Persistance
      this.sessions.set(senderId, sessionData);
      await this.saveSession(senderId, sessionData);

      console.log(` Session ACCEPTE & SAUVEGARDE: ${senderId}`);
    } catch (error) {
      console.error(` acceptSession ${senderId}:`, error);
      throw error;
    }
  }

  /**
   *  encryptMessage CORRIGE : Inclut TOUJOURS encryptedSessionKey
   */
  async encryptMessage(recipientId, plaintext) {
    const session = this.sessions.get(recipientId);
    if (!session) {
      throw new Error(`Session manquante pour ${recipientId}. Appelez initSession() d'abord.`);
    }

    const encoder = new TextEncoder();
    const plaintextBytes = encoder.encode(plaintext);
    const iv = window.crypto.getRandomValues(new Uint8Array(12));

    const ciphertext = await window.crypto.subtle.encrypt(
      { name: 'AES-GCM', iv, tagLength: 128 },
      session.sessionKey,
      plaintextBytes
    );

    session.messageCounter++;
    await this.saveSession(recipientId, session); //  Update counter

    return {
      ciphertext: this.arrayBufferToBase64(ciphertext),
      iv: this.arrayBufferToBase64(iv),
      counter: session.messageCounter,
      encryptedSessionKey: session.encryptedSessionKey //  CL MATRESSE
    };
  }

  /**
   *  decryptMessage simplifi pour la simulation :
   * on reconstruit la cl de session AES  chaque fois
   *  partir de encryptedSessionKey + cl prive RSA locale.
   */
  async decryptMessage(senderId, encryptedData, myPrivateKey = null) {
    if (!encryptedData.encryptedSessionKey) {
      throw new Error('encryptedSessionKey manquante');
    }
    if (!myPrivateKey) {
      throw new Error('Cl prive RSA manquante ct destinataire');
    }

    try {
      // 1) Reconstituer la cl AES  partir de la cl de session chiffre
      const encryptedSessionKey = this.base64ToArrayBuffer(encryptedData.encryptedSessionKey);
      const sessionKeyRaw = await window.crypto.subtle.decrypt(
        { name: 'RSA-OAEP' },
        myPrivateKey,
        encryptedSessionKey
      );

      const sessionKey = await window.crypto.subtle.importKey(
        'raw',
        sessionKeyRaw,
        { name: 'AES-GCM', length: 256 },
        false,
        ['decrypt']
      );

      // 2) Dchiffrer le message avec cette cl de session phmre
      const ciphertext = this.base64ToArrayBuffer(encryptedData.ciphertext);
      const iv = this.base64ToArrayBuffer(encryptedData.iv);

      const plaintextBytes = await window.crypto.subtle.decrypt(
        { name: 'AES-GCM', iv, tagLength: 128 },
        sessionKey,
        ciphertext
      );

      const decoder = new TextDecoder();
      return decoder.decode(plaintextBytes);
    } catch (error) {
      console.error(` decrypt ${senderId}:`, error);
      throw new Error(`Dchiffrement chou: ${error.message}`);
    }
  }

  // Utils Base64
  arrayBufferToBase64(buffer) {
    const bytes =
      buffer instanceof ArrayBuffer
        ? new Uint8Array(buffer)
        : ArrayBuffer.isView(buffer)
          ? new Uint8Array(buffer.buffer, buffer.byteOffset, buffer.byteLength)
          : new Uint8Array(buffer);
    let binary = '';
    for (let i = 0; i < bytes.byteLength; i++) {
      binary += String.fromCharCode(bytes[i]);
    }
    return btoa(binary);
  }

  base64ToArrayBuffer(base64) {
    const binary = atob(base64);
    const bytes = new Uint8Array(binary.length);
    for (let i = 0; i < binary.length; i++) {
      bytes[i] = binary.charCodeAt(i);
    }
    return bytes.buffer;
  }

  // Cleanup
  cleanupExpiredSessions() {
    const now = Date.now();
    const maxAge = 24 * 60 * 60 * 1000;
    for (const [userId, session] of this.sessions.entries()) {
      if (now - session.createdAt > maxAge) {
        this.sessions.delete(userId);
        if (this.db) {
          const tx = this.db.transaction(['sessions'], 'readwrite');
          tx.objectStore('sessions').delete(userId);
        }
      }
    }
  }

  getSessionStatus(userId) {
    const session = this.sessions.get(userId);
    return session ? 'active' : 'pending';
  }
}
