/**
 * HKDF (HMAC-based Key Derivation Function) - RFC 5869
 * 
 * Drive des cls cryptographiques  partir d'un matriel initial.
 * Utilis pour crer plusieurs cls indpendantes  partir d'un secret partag.
 * 
 * @param {ArrayBuffer} inputKeyMaterial - Matriel de cl initial (IKM)
 * @param {ArrayBuffer} salt - Sel cryptographique (optionnel mais recommand)
 * @param {ArrayBuffer} info - Information contextuelle pour lier la cl  son usage
 * @param {number} length - Longueur de la cl de sortie en octets
 * @returns {Promise<ArrayBuffer>} Cl drive
 */
export async function hkdf(inputKeyMaterial, salt, info, length) {
  // 1. Extract: HMAC-Hash(salt, IKM) -> PRK (Pseudo-Random Key)
  const extractKey = await crypto.subtle.importKey(
    'raw',
    salt,
    { name: 'HMAC', hash: 'SHA-256' },
    false,
    ['sign']
  );
  
  const prk = await crypto.subtle.sign('HMAC', extractKey, inputKeyMaterial);

  // 2. Expand: Gnre la cl de sortie de la longueur dsire
  const prkKey = await crypto.subtle.importKey(
    'raw',
    prk,
    { name: 'HMAC', hash: 'SHA-256' },
    false,
    ['sign']
  );

  const hashLength = 32; // SHA-256 = 32 bytes
  const iterations = Math.ceil(length / hashLength);
  
  if (iterations > 255) {
    throw new Error('HKDF: longueur de sortie trop grande');
  }

  let t = new Uint8Array(0);
  const result = new Uint8Array(length);
  let resultOffset = 0;

  for (let i = 1; i <= iterations; i++) {
    // T(i) = HMAC-Hash(PRK, T(i-1) | info | i)
    const input = new Uint8Array(t.length + info.byteLength + 1);
    input.set(new Uint8Array(t), 0);
    input.set(new Uint8Array(info), t.length);
    input[t.length + info.byteLength] = i;

    t = new Uint8Array(await crypto.subtle.sign('HMAC', prkKey, input));
    
    const bytesToCopy = Math.min(hashLength, length - resultOffset);
    result.set(t.subarray(0, bytesToCopy), resultOffset);
    resultOffset += bytesToCopy;
  }

  return result.buffer;
}

/**
 * Drive une cl AES-GCM  partir d'un matriel de cl
 * 
 * @param {ArrayBuffer} keyMaterial - Matriel de cl source
 * @param {string} context - Contexte de drivation (ex: "MessageKey-42")
 * @returns {Promise<CryptoKey>} Cl AES-GCM de 256 bits
 */
export async function deriveAESKey(keyMaterial, context) {
  const encoder = new TextEncoder();
  const salt = new Uint8Array(32); // Salt de zros pour la drivation KDF
  const info = encoder.encode(context);
  
  const derivedBytes = await hkdf(keyMaterial, salt, info, 32);
  
  return await crypto.subtle.importKey(
    'raw',
    derivedBytes,
    { name: 'AES-GCM', length: 256 },
    false,
    ['encrypt', 'decrypt']
  );
}

/**
 * Drive une cl HMAC  partir d'un matriel de cl
 * 
 * @param {ArrayBuffer} keyMaterial - Matriel de cl source
 * @param {string} context - Contexte de drivation
 * @returns {Promise<CryptoKey>} Cl HMAC-SHA256
 */
export async function deriveHMACKey(keyMaterial, context) {
  const encoder = new TextEncoder();
  const salt = new Uint8Array(32);
  const info = encoder.encode(context);
  
  const derivedBytes = await hkdf(keyMaterial, salt, info, 32);
  
  return await crypto.subtle.importKey(
    'raw',
    derivedBytes,
    { name: 'HMAC', hash: 'SHA-256' },
    false,
    ['sign', 'verify']
  );
}

/**
 * Calcule le HMAC d'un message
 * 
 * @param {CryptoKey} key - Cl HMAC
 * @param {ArrayBuffer} data - Donnes  authentifier
 * @returns {Promise<ArrayBuffer>} MAC (32 bytes)
 */
export async function computeHMAC(key, data) {
  return await crypto.subtle.sign('HMAC', key, data);
}

/**
 * Vrifie le HMAC d'un message
 * 
 * @param {CryptoKey} key - Cl HMAC
 * @param {ArrayBuffer} data - Donnes  vrifier
 * @param {ArrayBuffer} mac - MAC  vrifier
 * @returns {Promise<boolean>} true si valide
 */
export async function verifyHMAC(key, data, mac) {
  return await crypto.subtle.verify('HMAC', key, mac, data);
}

/**
 * Chiffre des donnes avec AES-GCM (AEAD - Authenticated Encryption)
 * 
 * AES-GCM fournit  la fois confidentialit ET authenticit.
 * Pas besoin de HMAC spar contrairement  AES-CBC.
 * 
 * @param {CryptoKey} key - Cl AES-GCM
 * @param {ArrayBuffer} plaintext - Donnes en clair
 * @param {ArrayBuffer} associatedData - Donnes authentifies mais non chiffres (AAD)
 * @returns {Promise<{ciphertext: ArrayBuffer, iv: ArrayBuffer}>}
 */
export async function encryptAESGCM(key, plaintext, associatedData = new ArrayBuffer(0)) {
  // IV de 96 bits (recommand pour AES-GCM)
  const iv = crypto.getRandomValues(new Uint8Array(12));
  
  const ciphertext = await crypto.subtle.encrypt(
    {
      name: 'AES-GCM',
      iv: iv,
      additionalData: associatedData,
      tagLength: 128 // Tag d'authentification de 128 bits
    },
    key,
    plaintext
  );

  return { ciphertext, iv: iv.buffer };
}

/**
 * Dchiffre des donnes avec AES-GCM
 * 
 * @param {CryptoKey} key - Cl AES-GCM
 * @param {ArrayBuffer} ciphertext - Donnes chiffres (inclut le tag)
 * @param {ArrayBuffer} iv - Vecteur d'initialisation
 * @param {ArrayBuffer} associatedData - Donnes associes (AAD)
 * @returns {Promise<ArrayBuffer>} Donnes dchiffres
 * @throws {Error} Si l'authentification choue
 */
export async function decryptAESGCM(key, ciphertext, iv, associatedData = new ArrayBuffer(0)) {
  return await crypto.subtle.decrypt(
    {
      name: 'AES-GCM',
      iv: iv,
      additionalData: associatedData,
      tagLength: 128
    },
    key,
    ciphertext
  );
}

/**
 * Gnre une paire de cls ECDH sur P-256
 * 
 * @returns {Promise<CryptoKeyPair>} Paire de cls publique/prive
 */
export async function generateECDHKeyPair() {
  return await crypto.subtle.generateKey(
    { name: 'ECDH', namedCurve: 'P-256' },
    true,
    ['deriveKey', 'deriveBits']
  );
}

/**
 * Effectue un change Diffie-Hellman sur courbe elliptique
 * 
 * @param {CryptoKey} privateKey - Cl prive locale
 * @param {CryptoKey} publicKey - Cl publique distante
 * @returns {Promise<ArrayBuffer>} Secret partag (32 bytes)
 */
export async function performECDH(privateKey, publicKey) {
  return await crypto.subtle.deriveBits(
    { name: 'ECDH', public: publicKey },
    privateKey,
    256 // 256 bits = 32 bytes
  );
}

/**
 * Exporte une cl publique au format JWK
 * 
 * @param {CryptoKey} publicKey - Cl publique  exporter
 * @returns {Promise<Object>} Cl au format JWK
 */
export async function exportPublicKey(publicKey) {
  return await crypto.subtle.exportKey('jwk', publicKey);
}

/**
 * Importe une cl publique depuis le format JWK
 * 
 * @param {Object} jwk - Cl au format JWK
 * @returns {Promise<CryptoKey>} Cl publique importe
 */
export async function importPublicKey(jwk) {
  return await crypto.subtle.importKey(
    'jwk',
    jwk,
    { name: 'ECDH', namedCurve: 'P-256' },
    true,
    []
  );
}

/**
 * Gnre des octets alatoires cryptographiquement scuriss
 * 
 * @param {number} length - Nombre d'octets  gnrer
 * @returns {Uint8Array} Octets alatoires
 */
export function randomBytes(length) {
  return crypto.getRandomValues(new Uint8Array(length));
}

/**
 * Concatne plusieurs ArrayBuffers
 * 
 * @param {...ArrayBuffer} buffers - Buffers  concatner
 * @returns {ArrayBuffer} Buffer concatn
 */
export function concatBuffers(...buffers) {
  const totalLength = buffers.reduce((sum, buf) => sum + buf.byteLength, 0);
  const result = new Uint8Array(totalLength);
  let offset = 0;
  
  for (const buffer of buffers) {
    result.set(new Uint8Array(buffer), offset);
    offset += buffer.byteLength;
  }
  
  return result.buffer;
}

/**
 * Compare deux ArrayBuffers en temps constant
 * Protection contre les timing attacks
 * 
 * @param {ArrayBuffer} a - Premier buffer
 * @param {ArrayBuffer} b - Second buffer
 * @returns {boolean} true si identiques
 */
export function constantTimeEqual(a, b) {
  if (a.byteLength !== b.byteLength) {
    return false;
  }
  
  const aBytes = new Uint8Array(a);
  const bBytes = new Uint8Array(b);
  let result = 0;
  
  for (let i = 0; i < aBytes.length; i++) {
    result |= aBytes[i] ^ bBytes[i];
  }
  
  return result === 0;
}