/**
 * crypto/sodium.js
 * 
 * Wrapper moderne autour de libsodium-wrappers pour messagerie scurise.
 * Utilise les primitives recommandes pour production :
 * - Curve25519 pour ECDH
 * - Ed25519 pour signatures
 * - XChaCha20-Poly1305 pour chiffrement authentifi
 */

import _sodium from 'libsodium-wrappers';

/**
 * Classe singleton pour grer l'initialisation de libsodium
 */
class SodiumCrypto {
  constructor() {
    this.ready = false;
    this.sodium = null;
  }

  /**
   * Initialise libsodium de manire asynchrone
   * DOIT tre appel avant toute opration crypto
   */
  async initialize() {
    if (this.ready) return;
    
    try {
      await _sodium.ready;
      this.sodium = _sodium;
      this.ready = true;
    } catch (error) {
      throw new Error(`chec initialisation libsodium: ${error.message}`);
    }
  }

  /**
   * Vrifie que libsodium est prt
   * @throws {Error} Si libsodium n'est pas initialis
   */
  ensureReady() {
    if (!this.ready || !this.sodium) {
      throw new Error('Libsodium pas initialis. Appelez initialize() d\'abord.');
    }
  }

  /**
   * Gnre une paire de cls Curve25519 pour ECDH
   * @returns {{publicKey: Uint8Array, privateKey: Uint8Array}}
   */
  generateKeyPairCurve25519() {
    this.ensureReady();
    
    try {
      const keyPair = this.sodium.crypto_box_keypair();
      
      return {
        publicKey: keyPair.publicKey,  // 32 bytes
        privateKey: keyPair.privateKey  // 32 bytes
      };
    } catch (error) {
      throw new Error(`chec gnration Curve25519: ${error.message}`);
    }
  }

  /**
   * Gnre une paire de cls Ed25519 pour signatures
   * @returns {{publicKey: Uint8Array, privateKey: Uint8Array}}
   */
  generateKeyPairEd25519() {
    this.ensureReady();
    
    try {
      const keyPair = this.sodium.crypto_sign_keypair();
      
      return {
        publicKey: keyPair.publicKey,  // 32 bytes
        privateKey: keyPair.privateKey  // 64 bytes
      };
    } catch (error) {
      throw new Error(`chec gnration Ed25519: ${error.message}`);
    }
  }

  /**
   * Calcule un secret partag via ECDH Curve25519
   * 
   * @param {Uint8Array} myPrivateKey - Ma cl prive (32 bytes)
   * @param {Uint8Array} theirPublicKey - Cl publique du pair (32 bytes)
   * @returns {Uint8Array} Secret partag (32 bytes)
   * 
   * Utilise crypto_scalarmult pour ECDH scuris.
   * Le rsultat DOIT passer par HKDF avant utilisation.
   */
  deriveSharedSecret(myPrivateKey, theirPublicKey) {
    this.ensureReady();
    
    // Validation des entres
    if (!(myPrivateKey instanceof Uint8Array) || myPrivateKey.length !== 32) {
      throw new Error('Cl prive invalide: doit tre Uint8Array de 32 bytes');
    }
    if (!(theirPublicKey instanceof Uint8Array) || theirPublicKey.length !== 32) {
      throw new Error('Cl publique invalide: doit tre Uint8Array de 32 bytes');
    }

    try {
      const sharedSecret = this.sodium.crypto_scalarmult(myPrivateKey, theirPublicKey);
      
      // Vrification contre weak keys (all-zero output)
      if (this.isAllZeros(sharedSecret)) {
        throw new Error('Secret partag faible dtect (contributeur invalide)');
      }
      
      return sharedSecret;
    } catch (error) {
      throw new Error(`chec ECDH: ${error.message}`);
    }
  }

  /**
   * Drive une cl via HKDF-SHA256
   * 
   * @param {Uint8Array} inputKeyMaterial - Matriel source (ex: secret ECDH)
   * @param {Uint8Array} salt - Salt optionnel (null = zeros)
   * @param {Uint8Array} info - Contexte d'application
   * @param {number} length - Longueur de sortie en bytes (dfaut: 32)
   * @returns {Uint8Array} Cl drive
   * 
   * Utilise HKDF standardis pour drivation scurise.
   */
  hkdf(inputKeyMaterial, salt, info, length = 32) {
    this.ensureReady();

    if (length < 1 || length > 255 * 32) {
      throw new Error(`Longueur HKDF invalide: ${length} (max: ${255 * 32})`);
    }

    try {
      // Utilise crypto_kdf pour drivation
      // Note: libsodium n'a pas HKDF natif, on utilise KDF interne ou on implmente
      const subkeyId = 0; // Peut tre utilis pour driver plusieurs cls
      const ctx = this.padContext(info);
      
      return this.sodium.crypto_kdf_derive_from_key(
        length,
        subkeyId,
        ctx,
        inputKeyMaterial
      );
    } catch (error) {
      // Fallback: implmentation HKDF manuelle
      return this.hkdfManual(inputKeyMaterial, salt, info, length);
    }
  }

  /**
   * Implmentation HKDF manuelle (RFC 5869)
   */
  hkdfManual(ikm, salt, info, length) {
    this.ensureReady();

    // HKDF-Extract: PRK = HMAC-Hash(salt, IKM)
    const actualSalt = salt || new Uint8Array(32); // Salt par dfaut
    const prk = this.sodium.crypto_auth(ikm, actualSalt);

    // HKDF-Expand: driver la longueur demande
    const n = Math.ceil(length / 32);
    let okm = new Uint8Array(0);
    let t = new Uint8Array(0);

    for (let i = 1; i <= n; i++) {
      const message = new Uint8Array([...t, ...info, i]);
      t = this.sodium.crypto_auth(message, prk);
      okm = new Uint8Array([...okm, ...t]);
    }

    return okm.slice(0, length);
  }

  /**
   * Chiffre un message avec XChaCha20-Poly1305
   * 
   * @param {Uint8Array|string} message - Message  chiffrer
   * @param {Uint8Array} key - Cl de chiffrement (32 bytes)
   * @param {Uint8Array} additionalData - Donnes associes (AD) optionnelles
   * @returns {{ciphertext: Uint8Array, nonce: Uint8Array}} Texte chiffr + nonce
   * 
   * XChaCha20-Poly1305 offre:
   * - Nonce de 192 bits (vs 96 pour ChaCha20)
   * - MAC authentifi intgr
   * - Rsistant aux attaques par collision de nonce
   */
  encrypt(message, key, additionalData = null) {
    this.ensureReady();

    // Validation
    if (!(key instanceof Uint8Array) || key.length !== 32) {
      throw new Error('Cl de chiffrement invalide: doit tre 32 bytes');
    }

    try {
      // Conversion message si string
      const messageBytes = typeof message === 'string' 
        ? this.sodium.from_string(message) 
        : message;

      // Gnre un nonce alatoire unique (192 bits)
      const nonce = this.sodium.randombytes_buf(
        this.sodium.crypto_aead_xchacha20poly1305_ietf_NPUBBYTES
      );

      // Chiffrement authentifi
      const ciphertext = this.sodium.crypto_aead_xchacha20poly1305_ietf_encrypt(
        messageBytes,
        additionalData,
        null, // secret nonce (non utilis en mode AEAD)
        nonce,
        key
      );

      return { ciphertext, nonce };

    } catch (error) {
      // Nettoyage en cas d'erreur
      this.secureZero(key);
      throw new Error(`chec chiffrement: ${error.message}`);
    }
  }

  /**
   * Dchiffre un message XChaCha20-Poly1305
   * 
   * @param {Uint8Array} ciphertext - Texte chiffr
   * @param {Uint8Array} nonce - Nonce utilis (192 bits)
   * @param {Uint8Array} key - Cl de dchiffrement (32 bytes)
   * @param {Uint8Array} additionalData - Donnes associes (AD) optionnelles
   * @returns {Uint8Array} Message dchiffr
   * @throws {Error} Si authentification choue
   */
  decrypt(ciphertext, nonce, key, additionalData = null) {
    this.ensureReady();

    // Validation
    if (!(key instanceof Uint8Array) || key.length !== 32) {
      throw new Error('Cl de dchiffrement invalide: doit tre 32 bytes');
    }

    if (!(nonce instanceof Uint8Array) || 
        nonce.length !== this.sodium.crypto_aead_xchacha20poly1305_ietf_NPUBBYTES) {
      throw new Error('Nonce invalide pour XChaCha20-Poly1305');
    }

    try {
      const plaintext = this.sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
        null, // secret nonce
        ciphertext,
        additionalData,
        nonce,
        key
      );

      return plaintext;

    } catch (error) {
      // Nettoyage en cas d'erreur
      this.secureZero(key);
      throw new Error('chec dchiffrement ou MAC invalide');
    }
  }

  /**
   * Signe un message avec Ed25519
   * 
   * @param {Uint8Array|string} message - Message  signer
   * @param {Uint8Array} privateKey - Cl prive Ed25519 (64 bytes)
   * @returns {Uint8Array} Signature (64 bytes)
   */
  sign(message, privateKey) {
    this.ensureReady();

    try {
      const messageBytes = typeof message === 'string'
        ? this.sodium.from_string(message)
        : message;

      return this.sodium.crypto_sign_detached(messageBytes, privateKey);

    } catch (error) {
      throw new Error(`chec signature: ${error.message}`);
    }
  }

  /**
   * Vrifie une signature Ed25519
   * 
   * @param {Uint8Array} signature - Signature  vrifier (64 bytes)
   * @param {Uint8Array|string} message - Message original
   * @param {Uint8Array} publicKey - Cl publique Ed25519 (32 bytes)
   * @returns {boolean} true si signature valide
   */
  verify(signature, message, publicKey) {
    this.ensureReady();

    try {
      const messageBytes = typeof message === 'string'
        ? this.sodium.from_string(message)
        : message;

      return this.sodium.crypto_sign_verify_detached(
        signature,
        messageBytes,
        publicKey
      );

    } catch (error) {
      return false;
    }
  }

  /**
   * Efface de manire scurise un buffer en mmoire
   * 
   * @param {Uint8Array} buffer - Buffer  effacer
   * 
   * Critique pour viter fuite de cls en mmoire.
   */
  secureZero(buffer) {
    if (!buffer || !(buffer instanceof Uint8Array)) return;
    
    try {
      this.sodium.memzero(buffer);
    } catch (error) {
      // Fallback: crasement manuel
      for (let i = 0; i < buffer.length; i++) {
        buffer[i] = 0;
      }
    }
  }

  /**
   * Gnre des bytes alatoires cryptographiquement scuriss
   * 
   * @param {number} length - Nombre de bytes
   * @returns {Uint8Array}
   */
  randomBytes(length) {
    this.ensureReady();
    return this.sodium.randombytes_buf(length);
  }

  /**
   * Convertit Uint8Array en base64
   */
  toBase64(bytes) {
    this.ensureReady();
    return this.sodium.to_base64(bytes);
  }

  /**
   * Convertit base64 en Uint8Array
   */
  fromBase64(base64String) {
    this.ensureReady();
    return this.sodium.from_base64(base64String);
  }

  /**
   * Convertit Uint8Array en string hex
   */
  toHex(bytes) {
    this.ensureReady();
    return this.sodium.to_hex(bytes);
  }

  /**
   * Convertit hex string en Uint8Array
   */
  fromHex(hexString) {
    this.ensureReady();
    return this.sodium.from_hex(hexString);
  }

  // === UTILITAIRES INTERNES ===

  /**
   * Vrifie si un buffer est rempli de zros
   */
  isAllZeros(buffer) {
    for (let i = 0; i < buffer.length; i++) {
      if (buffer[i] !== 0) return false;
    }
    return true;
  }

  /**
   * Pad le contexte pour crypto_kdf (doit tre exactement 8 bytes)
   */
  padContext(context) {
    const ctx = new Uint8Array(8);
    const input = typeof context === 'string' 
      ? this.sodium.from_string(context) 
      : context;
    
    for (let i = 0; i < Math.min(8, input.length); i++) {
      ctx[i] = input[i];
    }
    
    return ctx;
  }
}

// Export singleton
const sodium = new SodiumCrypto();
export default sodium;